import React, { Component } from 'react'
import '../../css/userDialog.css'
import {signUp} from './LeanCloud'

export default class UserDialog extends Component{
  constructor(props){
    super(props)
    this.state = {
      selected: 'signUp',
      formDate: {
          userName: '',
          passWord: ''
      }
    }
  }
  // / 
  switch(e){
    this.setState({
      selected: e.target.value
    })
  }

  //
  signIn(e){
    e.preventDefault();
    console.log('')
  }

  //
  signUp(event){
    event.preventDefault();

    let {userName, passWord} = this.state.formDate

    let success = (user)=>{
        // console.log('')
        // console.log(user)
        this.props.onSignUp.call(null, user)
    }

    let error = (error)=>{
        console.log('')
        console.log(error)
    }

    signUp(userName, passWord, success, error)

    console.log('')
  }

  //
  changeFormData(key, event){
    let curState = JSON.parse(JSON.stringify(this.state))

    curState.formDate[key] = event.target.value

    // console.log(JSON.stringify(curState))

    this.setState(curState)
  }

  render(){
    let signUpFrom = (
        <form className="signUp" onSubmit={this.signUp.bind(this)}> {/* */}
            <div className="row">
                <label></label> 
                <input type="text" value={this.state.formDate.userName} onChange={this.changeFormData.bind(this, 'userName')}/>
                {/* bind  this */}
            </div>
            <div className="row">
                <label></label>
                <input type="password" value={this.state.formDate.passWord} onChange={this.changeFormData.bind(this, 'passWord')}/>
            </div>
            <div className="row actions">
                <button type="submit"></button>
            </div>
        </form>
    )
    let signInFrom = (
        <form className="signIn" onSubmit={this.signIn.bind(this)}> {/* */}
            <div className="row">
                <label></label>
                <input type="text" value={this.state.formDate.userName} onChange={this.changeFormData.bind(this, 'userName')}/>
            </div>
            <div className="row">
                <label></label>
                <input type="password" value={this.state.formDate.passWord} onChange={this.changeFormData.bind(this, 'passWord')}/>
            </div>
            <div className="row actions">
                <button type="submit"></button>
            </div>
        </form>
    )
    return (
      <div className="UserDialog-Wrapper">
        <div className="UserDialog">
          <nav>
            <label><input type="radio" value="signUp" checked={this.state.selected === 'signUp'} onChange={this.switch.bind(this)}/> </label>
            <label><input type="radio" value="signIn" checked={this.state.selected === 'signIn'} onChange={this.switch.bind(this)}/> </label>
          </nav>
          <div className="panes">
            {this.state.selected === 'signUp' ? signUpFrom : null}
            {this.state.selected === 'signIn' ? signInFrom : null}
          </div>
        </div>
      </div>
    )
  }
}


